// #region
* {
    font-family: "Noto Sans", Arial, Helvetica, sans-serif;
    font-weight: 400;
}

h3 {
    font-family: "Google Sans", Arial, Helvetica, sans-serif;
    font-weight: 700;
    text-align: center;
}

::-webkit-scrollbar {
    display: none;
}

hr {
    margin: 1em 0 !important;
    border: 1px solid rgb(180, 180, 180);
}

body {
    margin: 0;
}

.container {
    width: 80%;
    max-width: 800px;
    margin: 0 auto;
}

a {
    transition: color .2s;
    text-decoration: none;
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important;

    &:hover {
        text-decoration: none;
    }
}

// #endregion

// #region custom checkbox
@keyframes click-wave {
    0% {
        height: 40px;
        width: 40px;
        opacity: 0.35;
        position: relative;
    }

    100% {
        height: 200px;
        width: 200px;
        margin-left: -80px;
        margin-top: -80px;
        opacity: 0;
    }
}

.option-input {
    $buttonSize: 20px;
    $checkMarkSize: 14px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    position: relative;
    height: $buttonSize;
    width: $buttonSize;
    transition: all 0.15s ease-out 0s;
    background: #cbd1d8;
    border: none;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    outline: none;
    position: relative;
    // z-index: 1000;

    &:hover {
        background: #9faab7;
    }

    &:checked {
        background: #40e0d0;
    }

    &:focus {
        outline: 0 !important;
    }

    &:checked::before {
        height: $buttonSize;
        width: $buttonSize;
        position: absolute;
        content: 'âœ”';
        display: inline-block;
        font-size: $checkMarkSize;
        text-align: center;
        line-height: $buttonSize;
    }

    .option-input:checked::after {
        -webkit-animation: click-wave 0.65s;
        -moz-animation: click-wave 0.65s;
        animation: click-wave 0.65s;
        background: #40e0d0;
        content: '';
        display: block;
        position: relative;
        z-index: 100;
    }

    &.radio {
        border-radius: 50%;

        &::after {
            border-radius: 50%;
        }
    }
}

// #endregion

label {
    font-weight: normal;
    width: fit-content
}

.flexRow {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.flexCol {
    display: flex;
    flex-direction: column;
}

.step1 {
    .opHeader {
        margin: 10px 0;
    }

    .ImOp1 {
        select {
            max-width: 200px;
        }

        input {
            max-width: 275px;
        }
    }

    .ImOp2 {
        #btnUrl {
            margin-left: 10px;
        }
    }

    .ImOp3 {
        .clearNdemo {
            margin: 10px auto 0 auto;
            width: fit-content;
        }
    }
}

.step2 {
    &.scheduler-border {
        label {
            margin-bottom: 0;
        }

        input {
            margin-top: 0;
        }
    }

    .spacingBetweenURLs {
        display: flex;
        flex-direction: row;
        margin-top: 10px;
        flex-wrap: wrap;
        justify-content: center;
        label:not(.custom) {
            display: flex;
            align-items: center;
            padding: .5em;
            margin: 5px;
            border: 1px solid rgb(156, 156, 156);
            border-radius: 5px;
        }
    }

    .otherOptions {
        label:not(.whitelist) {
            display: flex;
            align-items: center;
            margin: 10px 0;
        }
    }
}

.step3 {
    display: flex;
    align-items: center;

    .exportBtn {
        margin-bottom: 10px;
    }
}

.btn {
    transition: background-color .2s ease-out;
    background-color: #40e0d0;
    border: none;

    &:hover {
        background-color: #3dc9bb;
    }
}

header {
    width: 100%;
    position: sticky;
    top: 0;
    text-align: center;
    background-color: rgba($color: #3dc9bb, $alpha: 0.5);
    backdrop-filter: blur(10px);

    a {
        font-family: "Google Sans", Arial, Helvetica, sans-serif;
        font-weight: 700;
        font-size: 50px;
    }

    text-transform: uppercase;

    .product {
        text-align: center;
        width: fit-content;
        margin: 0 auto 10px auto;
    }
}

a:hover,
a:visited,
a:link,
a:active {
    text-decoration: none;
}